.web-chat-hoop {
    $this: &;
    height: auto;

    .eureka-tile__content-container {
        display: flex;
        justify-content: center;
        align-items: center;

        .eureka-tile__content-container-inner {
            padding-top: 0;
            padding-bottom: 0;
        }
    }

    &__hoop-content {
        text-align: center;

        #{$this}__headText {
            margin: 0;
            font-size: 2rem;
        }

        #{$this}__subText {
            font-size: 1.5rem;
            margin-top: 8px;
        }

        #{$this}__btn {
            width: 343px;
            color: var(--eureka-core-color-primary);
            margin-bottom: 16px;
        }
    }
}
