@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/mixins/layout.scss';

.web-chat-leave-message {
    $this: &;

    .eureka-tile__content-container {
        display: flex;
        justify-content: center;
        align-items: center;
    }

    &__leave-message-content {
        text-align: center;

        #{$this}__headText {
            margin: 0;
            font-size: 2rem;
        }

        #{$this}__subText {
            font-size: 1.5rem;
            margin-top: 8px;
        }

        // TODO: will be replaced by Eureka UI textbox
        #{$this}__textarea {
            resize: none;
            border-color: #e5e5e5;
            padding: 1rem;
            border-radius: var(--eureka-layout-tile-border-radius, 20px);
        }

        // TODO: will be replaced by Eureka UI textbox
        #{$this}__textarea:focus {
            outline: none;
        }

        #{$this}__btn {
            width: 343px;
            // color: var(--eureka-core-color-primary);
            margin-top: 12px;
        }
    }
}
