@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/mixins/layout.scss';

.web-chat-generic-message {
    $this: &;

    .eureka-tile__content-container {
        height: 362px;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    &__message-content {
        text-align: center;

        #{$this}__headText {
            margin: 0;
            font-size: 2rem;
        }

        #{$this}__headText #{$this}__headText--withNoSubText {
            font-size: 1.7rem;
        }

        #{$this}__subText {
            font-size: 1.5rem;
            margin-top: 8px;
        }
    }
}

.web-chat-generic-message.web-chat-generic-message--withNoSubText {
    .web-chat-generic-message__headText {
        font-size: 1.7rem;
    }
}
